function parseTsv(data, widths) {
  var table = data.split('\n').map(function (line) {
    return line.split('\t').map(function (e) {
      return e.trim();
    });
  });
  var pages = widths.map(function (val, pageNb) {
    var lowBound = widths.slice(0, pageNb).reduce(function (a, b) {
      return a + b;
    }, 0);
    var hiBound = lowBound + val;
    var page = table.map(function (line) {
      return line.slice(lowBound, hiBound);
    });
    var noWhiteLinesPage = page.filter(function (line) {
      return line.join('').trim() !== '';
    });
    var pageOfObjects = noWhiteLinesPage.slice(1).map(function (line) {
      var obj = {};
      noWhiteLinesPage[0].forEach(function (label, i) {
        obj[label] = line[i];
      });
      return obj;
    });
    return pageOfObjects;
  });
  return pages;
}

module.exports = parseTsv;